<script>
import {
  mdbBtn,
  mdbCard,
  mdbCardBody,
  mdbCardText,
  mdbIcon,
  mdbBreadcrumb,
  mdbBreadcrumbItem,
} from "mdbvue";
import images from "../components/imagenes";

export default {
  components: {
    mdbBtn,
    mdbCard,
    mdbCardBody,
    mdbCardText,
    mdbIcon,
    mdbBreadcrumb,
    mdbBreadcrumbItem,
    images,
  },
  data() {
    return {
      fourRandom: [],
      id: this.$route.params.id,
      product: {},
      allImages: [
        "1.jpg",
        "2.jpg",
        "3.jpg",
        "4.jpg",
        "5.jpg",
        "6.jpg",
        "7.jpg",
        "8.jpg",
        "9.jpg",
        "10.jpg",
        "11.jpg",
        "12.jpg",
        "13.jpg",
        "16.jpg",
      ],
      images: [],
    };
  },
  props: {
    items: Array,
  },
  mounted() {
    this.getProduct();
    this.setTitle();
  },
  updated() {
    this.setTitle();
  },
  watch: {
    $route(to) {
      this.id = to.params.id;
      this.getProduct();
    },
  },
  methods: {
    getProduct() {
      this.items.forEach((item) => {
        if (item.id == this.id) {
          this.product = item;
        }
      });
    },
    setTitle() {
      document.title = `Cercas y Mallas Versatiles - ${this.product.title}`;
    },
  },
};
</script>

